import React, { Component } from 'react';

import './default.scss';

export default class Terms extends Component {
  constructor(props){
    super(props);

    this.state = {
      activeIndex: -1
    };
  }

  setActiveIndex = index => {
    this.setState({
      activeIndex: index
    });
  };

  scrollTo = index => {
    const node = this.refs[index];
    window.scrollTo(0, node.offsetTop - 100);
  };

  renderTerm({ title, text, subitems }, index){
    return (
      <div className='term__container' key={index} ref={index}>
        <h2 className='small'>{title}</h2>

        <p
          className='copytext-small'
          dangerouslySetInnerHTML={{ __html: text }}
        />

        {subitems &&
          subitems.map((item, i) => (
            <div className='termSub__container' key={i}>
              <h3 className='small'>{item.title}</h3>

              <p
                className='copytext-small'
                dangerouslySetInnerHTML={{ __html: item.text }}
              />
            </div>
          ))}
      </div>
    );
  }

  renderNav(items){
    return (
      <ul>
        {items.map((item, index) => (
          <li
            className='termNav__item link-small'
            key={index}
            onClick={() => this.scrollTo(index)}
          >
            {item.title.replace(/\.$/, '')}
          </li>
        ))}
      </ul>
    );
  }

  render(){
    const { data } = this.props;
    const page = data.markdownRemark;
    const { title, updatedDt, items, description } = page.frontmatter;

    return (
      <div className='terms__container'>
        <div className='terms__nav'>{this.renderNav(items)}</div>

        <h1>{title}</h1>

        <div className='terms__content'>
          <h3 className='small'>{`Last Updated Date: ${updatedDt}`}</h3>

          <p
            className='copytext-small'
            dangerouslySetInnerHTML={{ __html: description }}
          />

          <div className='terms__content'>
            {items.map((item, index) => this.renderTerm(item, index))}
          </div>
        </div>
      </div>
    );
  }
}

export const query = graphql`
  query TermsPageQuery($slug: String!){
    markdownRemark(fields: { slug: { eq: $slug } }){
      html
      frontmatter {
        title
        updatedDt
        description
        items {
          title
          text
          subitems {
            title
            text
          }
        }
      }
    }
  }
`;



